.contacts-list {
  width: max-content;
  min-height: 60vh;
  margin: 0 auto;
  border: 3px solid #060b20;
  outline: 9px solid #4c3ce1;
  padding: 24px;
  border-radius: 30px;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(200, 252, 252, 1) 60%,
    rgb(61, 221, 253) 100%
  );
  background-attachment: fixed;
}

input[type='text'] {
  display: inline-block;
  padding: 5px 10px 5px 2px;
  border-radius: 50px;
  width: 50%;
  border: none;
  outline: none;
  background: transparent;
}
input[type='checkbox'] {
  height: 11px;
}

.img__search {
  display: inline-block;
  padding: 5px 0;
  height: 22px;
  width: 22px;
  color: #060b20;
}

.search-field-container {
  display: flex;
  column-gap: 2px;
  border: 1px solid #060b20;
  border-radius: 50px;
  padding-left: 10px;
  max-width: 220px;
  margin: 0 auto 20px;
}
h2{
  font-size: 1.1rem;
}
.checkBoxContainer{
display: flex;
gap: 5px;
justify-content: space-between;
align-items: center;
max-width: 230px;
}

fieldset{
  border:none;
}

label{
  font-size: 0.9rem;
}